'use strict';


export function categoryList(pIndex = 0, callback, errorCallback) {
    return (dispatch) => {
        axios.get('/api/category/list').then(function (response) {
            let data = response.data.result;
            if(callback){
              callback(data);
            }
          }).catch(function (error) {
            if(errorCallback) {
                errorCallback(error);
            }
          }).then(function () {
            // always executed
          })
    };
}

export function getData(category, callback, errorCallback) {
    return (dispatch) => {
        axios.post('/api/machine/machineList', {
          category: category
        })
        //.get('/api/machine/machineList')
        .then(function (response) {
            let data = response.data.result;
            if(callback){
              callback(data, category);
            }
          }).catch(function (error) {
            if(errorCallback) {
                errorCallback(error);
            }
          }).then(function () {
            // always executed
          })
    };
}


